import { cn } from '@/lib/utils';
import type React from 'react';

interface SectionProps extends React.HTMLAttributes<HTMLElement> {
  id: string;
  title: string;
  description?: string;
  children: React.ReactNode;
}

export function Section({ id, title, description, children, className, ...props }: SectionProps) {
  return (
    <section id={id} className={cn('py-16 md:py-24', className)} {...props}>
      <div className="container max-w-screen-xl mx-auto px-4">
        <div className="text-center mb-12">
          <h2 className="text-3xl md:text-4xl font-bold font-headline text-primary tracking-tight">{title}</h2>
          {description && <p className="mt-4 text-lg text-muted-foreground max-w-3xl mx-auto">{description}</p>}
        </div>
        {children}
      </div>
    </section>
  );
}
